<template>
  <div class="welcome welcome__expense">
    <h1>Welcome to Dailybread!</h1>

    <p>Please enter your first expense!</p>

    <expense-form
      :submitHandler="submitHandler"
      :editingExpense="editingExpense"
    />
  </div>
</template>

<script>
  import expenseForm from '../ledger/ExpenseForm';

  export default {
    data() {
      return {
      };
    },

    props: {
      submitHandler: {
        required: true,
      },

      editingExpense: {
        required: true,
      },
    },

    components: {
      expenseForm,
    },
  };
</script>

<style lang="scss">
</style>



